@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap');
@import './footer.css';

:root {
  --btn: #00FFAB;
  --scnd: #14C38E;
  --span: #7effd4;
  --primary: #5CC060;
  --second: #55e2b3;
  --font: #ffff;
  --order: #5B6E80;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #fcfcfc;
  text-decoration: none;
  box-sizing: border-box;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 60px;
  width: 100%;
  background-color: var(--scnd);
  position: fixed;
  z-index: 99999;
  top: 0;
}



.menu-icon {
  display: none;
}

.logo a {
  color: var(--font);
  text-decoration: none;
}


.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: var(--font);
  text-decoration: none;
}

.nav-elements ul a.active {
  color: var(--font);
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--second);
}

/* Hero section */

.hero-container {
  width: 100%;
  overflow: hidden;
  /* background-color: var(--scnd); */
  margin-top: 3.2rem;
  background: rgb(20, 195, 184);
  background: linear-gradient(0deg, rgba(20, 195, 184, 1) 0%, rgba(20, 195, 142, 1) 100%);
  height: 800px;
}

.hero-wrapper {
  display: flex;
  gap: 1rem;
  align-items: center;
  max-width: 1100px;
  margin: 0 auto;
  /* background-color: tomato; */
}

.hero-img>img {
  width: 600px;
  height: 600px;
  margin-right: -70px;
}

.text-content {
  display: flex;
  flex-direction: column;
  color: var(--font);
}

.tagline>h1 {
  font-size: 2.1rem;
}



.tagline>h1>span {
  color: var(--span);
}

.tagline>h3 {
  padding-top: 0px;
}

.text-content>p {
  /* text-align: justify; */
  font-weight: 600;
  padding-bottom: 1rem;
}

.text-content>button {
  width: 200px;
  background-color: var(--second);
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  color: var(--font);
  font-weight: 600;
  transition: 0.3s ease-in-out;
}

.text-content>button:hover {
  background-color: var(--btn);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

/* end Hero Section */

/* start Produk Section */
/* .produk-container {
  height: 1000px;
} */

.produk-wrapper {
  display: flex;
  gap: 1rem;
  align-items: center;
  max-width: 1100px;
  margin: 0 auto;
}

#produk {
  padding-top: 4rem;
  margin: 0 auto;
  color: var(--scnd);
  margin-bottom: 3rem;
}

/* end of produk */

/* card */
.card-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1100px;
  margin: 0 auto;
  margin-bottom: 8rem;
  gap: 1rem;
  flex-direction: row;
  justify-content: space-around;
  /* background-color: tomato; */
}


.card {
  width: 250px;
  height: 100%;
  /* border: 1px solid black; */
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  text-align: center;
  /* background-color: tomato; */
}

.card-wrapper {
  margin-bottom: 10px;
}

.card-wrapper h3 {
  color: var(--scnd);
}

.card-wrapper>a {
  color: var(--font);
  text-decoration: none;
  border-radius: 8px;
  background-color: var(--scnd);
  padding: 5px;
  transition: 0.3s ease-in-out;
}

.card-wrapper>a:hover {
  background-color: var(--btn);
  transition: 0.3s ease-in-out;
}

.card-wrapper>img {
  width: 250px;
  height: 250px;
}

#coret {
  text-decoration: line-through red;
}

.card-detail {
  max-width: 1100px;
  margin: 0 auto;
  margin-bottom: 7rem;
  display: flex;
  gap: 2rem;
}

.card-detail img {
  width: 400px;
  height: 400px;
}

.card-detail h1 {
  color: var(--scnd);
}

.detail-wrapper {
  margin-top: -30px;
}

.detail-wrapper>a {
  color: var(--font);
  text-decoration: none;
  border-radius: 8px;
  background-color: var(--scnd);
  padding: 5px;
  transition: 0.3s ease-in-out;
}

.detail-wrapper>a:hover {
  background-color: var(--btn);
  transition: 0.3s ease-in-out;
}

.detail-wrapper>p>span {
  color: salmon;
  font-weight: 600;
}

/* end of card  */

/* WhatsAppForm */

.form-container {
  color: var(--scnd);
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 1100px;
  margin: 0 auto;
  /* border: 1px solid black; */
  border-radius: 5px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.wa h3 {
  color: var(--scnd);
  align-items: center;
  text-align: center;
  padding-top: 100px;
}

form {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  margin: 2rem;
  gap: 10px;
}

label {
  display: flex;
  justify-content: space-between;
}

input,
textarea {
  width: 100%;
  height: 35px;
  border-radius: 5px;
  border: 1px solid var(--scnd);
}

textarea {
  height: 80px;
}

input::placeholder {
  color: var(--scnd);
}

textarea::placeholder {
  color: var(--scnd);
}

input:focus {
  outline: none !important;
  border: 1px solid var(--primary);
  box-shadow: 0 0 10px #719ECE;
}

textarea:focus {
  outline: none !important;
  border: 1px solid var(--primary);
  box-shadow: 0 0 10px #719ECE;
}

form>button {
  color: var(--font);
  font-size: large;
  font-weight: 600;
  height: 40px;
  background-color: var(--scnd);
  border-radius: 5px;
  border: 1px solid var(--scnd);
  transition: 0.3s ease-in-out;
}

form>button:hover {
  background-color: var(--btn);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

/* end WhatsAppForm */

/* order */

.orders>h3 {
  color: var(--scnd);
  text-align: center;
  padding-top: 100px;
}

.order-container {
  /* width: 1000px; */
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 1rem;
  display: flex;
  color: var(--order);
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 1100px;
  margin: 0 auto;
  margin-bottom: 10rem;
  padding: 60px;
}

.order-title {
  align-items: center;
  margin-bottom: 5rem;
}

.logo-wrapper {
  display: flex;
  gap: 2rem;
  margin-bottom: 4rem;
}

.logo-market img {

  padding: 1rem;
  margin-bottom: 4rem;
}

.logo-wrapper img {
  width: 100%;
}

/* end oforder */

/* testi */

#testi {
  color: var(--scnd);
  align-items: center;
  text-align: center;
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 50px;
}

.carousel-root {
  width: 64% !important;
  margin: auto !important;
  margin-top: 3% !important;
}

.carousel .slide {
  background: #fff !important;
  color: black;
  height: 100%;
}

.carousel .slide img {
  width: 139px !important;
  border-radius: 50%;
}

.myCarousel {
  background: #fafafa;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  border: 1px solid #ddd;
  height: 286px;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: 5% !important;
}

.myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: sans-serif;
  max-height: 67px;
}

.myCarousel p:before {
  content: "“";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "”";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: #454545 !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  background: #000 !important;
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

/* end testi */

/* footer */


.footer-container {
  margin-top: 20rem;
  background-color: #71c1a6;
  color: #485b53;
  height: 200px;
}

.footer-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  max-width: 1100px;
  margin: 0 auto;
}
.market-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 10px;
}

.market-footer a {
  display: flex;
  text-align: start;
  flex-direction: column;
  align-items: center;
}

.market-footer li {
  text-decoration: none;
  list-style: none;
}

.market-footer img {
  width: 35%;
}
/* footer */

@media only screen and (max-width: 934px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }

  .myCarousel {
    background: #fafafa;
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 269px;
  }

  .carousel .slide img {
    width: 24% !important;
    border-radius: 50%;
  }
}

/* testi */

@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }

  .hero-wrapper {
    flex-direction: column;
    max-height: 100%;
    margin-left: 1.3rem;
    margin-right: 1.3rem;
  }

  .hero-img>img {
    display: none;
  }

  .text-content {
    display: flex;
    flex-direction: column;
    color: var(--font);
    align-items: center;
  }

  .tagline>h1 {
    font-size: 1.8rem;
  }

  .card-detail {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px;
  }

  .card-detail img {
    width: 300px;
    height: 300px;
  }

  .order-container {
    margin: 10px;
    font-size: 80%;
  }

  .logo-wrapper img {
    width: 15%;
    height: auto;
  }

  .logo-market img {
    width: 15%;
    height: auto;
  }
}

@media (max-width: 600px) {
  .menu-icon {
    display: block;
    cursor: pointer;
    color: var(--font);
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: var(--scnd);
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 270px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}